html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: Helvetica Neue, Helvetica;
  color: #333;
}

.layout-sidebar {
  display: flex;
  align-items: stretch;
  min-height: 100%;
  min-width: 100%;
}

.layout-sidebar--sidebar {
  height: 100%;
  border-right: 1px solid #CCC;
  width: 250px;
}

.layout-absolute {
  position: relative;
}

.layout-absolute--bottom {
  position: absolute;
  bottom: 0px;
}

.pad {
  padding: 1em;
}

.pad-l {
  padding: 2em;
}

.pad-ew-l {
  padding-left: 2em;
  padding-right: 2em;
}

.spc-s {
  margin-bottom: 1em;
}

.text-center {
  text-align: center;
}


/* Default styles for a blog post */
.post {
  color: #333;
}

.post img {
  display: block;
  margin: 0 auto;
}

.post p {
 line-height: 150%;
 font-size: 16px;
}